import React, {Component} from 'react';
import { Link } from 'react-router-dom';

class Post extends Component {

    render() {
        const { titulo, contenido, _id } = this.props.post;
        const index = this.props.id;
        return (
            <div className="post justify-content-between d-flex mb-2 p-1">
                <p className="col-2">{index+1}</p>
                <div className="col-6 text-center">
                    <h5 className="">{titulo}</h5>
                    <p className="">{contenido}</p>
                </div>
                <div className="col-4 text-center">
                    <Link to={'/ver-tarea'} onClick={ () => this.props.mostrarPost(_id) } className="mr-1 mb-1 btn btn-primary">Ver</Link>
                    <Link to={'/editar-tarea'} onClick={ () => this.props.mostrarPost(_id) } className="mr-1 mb-1 btn btn-info">Editar</Link>
                    <button onClick={ () => this.props.borrarPost(_id) } className="mr-1 mb-1 btn btn-danger">Borrar &times;</button>
                </div>
            </div>
        )
    }
}

export default Post;