import React, {Component} from 'react';

import Header from './Header';
import ContenidoPost from './ContenidoPost';

class MostrarPost extends Component {
    render() {
        return (
            <React.Fragment>
                <Header />
                <ContenidoPost 
                    verPost={this.props.verPost}
                />
            </React.Fragment>
        )
    }
}

export default MostrarPost;