import React from 'react';
import { Link } from 'react-router-dom';

const Header = () => {
    return (

        <React.Fragment>

            <div className="row my-3 justify-content-between d-flex">
                <h2 className="col-md-9 text-center">Listado de Cosas por Hacer</h2>
                <div className="col-12 col-md-3 text-right">
                    <Link to={'/crear-tarea'} className="col-5 col-md-12 btn btn-success">Crear Tarea +</Link>
                </div>
            </div>

        </React.Fragment>

    )
}

export default Header;