import React, { Component } from 'react';
import  { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom';
import axios from 'axios';
import Swal from 'sweetalert2';

//Rutas
import Inicio from './inicio/Inicio';
import MostrarPost from './ver-post/MostrarPost';
import CrearPost from './crear-post/CrearPost';
import EditarPost from './editar-post/EditarPost';
import Footer from './footer/Footer';

class Router extends Component {

    state = {
        tareas: [],
        verPost: []
    }

    componentDidMount() {

        const post = localStorage.getItem("VerPost");
            if(post){
                this.setState({
                    verPost: JSON.parse(post)
                });
            }
        this.obtenerPosts();
    }

    componentDidUpdate() {

        this.obtenerPosts();
    }
    
    // ================
    // obtener todos los posts
    // ================
    obtenerPosts = () => {
        axios.get('https://crud-node-tareas.herokuapp.com/posts')
        // axios.get('http://localhost:5500/posts')
        .then( res => {
        //    console.log(res.data);
            this.setState({
                tareas: res.data.posts
            })
        } )
        .catch( error => {
            console.log(error);
        } );
    }
    
    // ================
    // crear un nuevo post
    // ================
    crearPost = (post) => {
        // axios.post('https://crud-node-tareas.herokuapp.com/post/create', post)
        axios.post('http://localhost:5500/post/create', post)
        .then( res => {
           console.log(res.data.ok)

            if(res.data.ok === true){

                Swal.fire({
                    position: 'top-end',
                    type: 'success',
                    title: 'Se creo correctamente la tarea',
                    showConfirmButton: false,
                    timer: 1500
                });

                this.setState({
                    tareas: res.data.posts,
                    ok: res.data.ok
                })
            }
        } )
        .catch( error => {
            console.log(error);
        } );

        // console.log(post);
    }
    
    // ================
    // Editar post
    // ================
    editarPost = (id, post) => {

        axios.put(`https://crud-node-tareas.herokuapp.com/post/update/${id}`, post)
        // axios.put(`http://localhost:5500/post/update/${id}`, post)
        .then( res => {
            if (res.data.ok === true ) {
                Swal.fire({
                    position: 'top-end',
                    type: 'success',
                    title: 'Se guardaron los cambios correctamente',
                    showConfirmButton: false,
                    timer: 1500
                });
                
            }

        } )
        .catch( error => {
            console.log(error);
        } );
    }

    // ================
    // Borrar post
    // ================
    borrarPost = (id) => {
    
        Swal.fire({
            title: 'Estas seguro?',
            text: "Esta acciÃ³n no se puede deshacer!",
            type: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Si, borrar!',
            cancelButtonText : 'Cancelar'
        }).then((result) => {
            if (result.value) {
                axios.delete(`https://crud-node-tareas.herokuapp.com/post/delete/${id}`)
                // axios.delete(`http://localhost:5500/post/delete/${id}`)
                    .then( res => {
                        if (res.data.ok === true ) {
                            Swal.fire(
                                'Eliminado!',
                                'El producto ha sido eliminado.',
                                'success'
                            )
                        }
                    } )
                    .catch( error => {
                        console.log(error);
                    } );
                
            }
        })
    }
    
    // ================
    // mostrar post
    // ================
    mostrarPost = (id) => {
        const tareas = [...this.state.tareas];
        
        let post = tareas.filter( data => (
            data._id === id
        ) );
    
        this.setState({
            verPost: post[0]
        });
        localStorage.setItem(
            "VerPost",
            JSON.stringify(post[0])
        )
    }


    render() {
        return (
            <BrowserRouter>
                <div className="container">
                    <Redirect to="/" />
                    <Switch>

                        <Route exact path={'/'} render = { () => (
                            <Inicio
                                tareas={this.state.tareas}
                                mostrarPost={this.mostrarPost}
                                borrarPost={this.borrarPost}
                            />
                        )}/>
                        <Route exact path={'/ver-tarea'} render = { () => (
                            <MostrarPost 
                                verPost={this.state.verPost}
                            />
                        )}/>
                        <Route exact path={'/crear-tarea'} render = { () => (
                            <CrearPost 
                                crearPost={this.crearPost}
                                
                            />
                        )}/>
                        <Route exact path={'/editar-tarea'} render = { () => (
                            <EditarPost 
                                editarPost={this.editarPost}
                                verPost={this.state.verPost}
                            />
                        )}/>

                    </Switch>

                </div>
            <Footer />
            
            </BrowserRouter>
        )
    }
}

export default Router;