import React, {Component} from 'react';
import { Link } from 'react-router-dom';
// import Swal from 'sweetalert2';

class ContenidoPost extends Component {

    tituloRef = React.createRef();
    contenidoRef = React.createRef();
    state = {
        mensaje: ""
    }

    crearTarea = (e) => {
        e.preventDefault();

        const post = {
            titulo: this.tituloRef.current.value,
            contenido: this.contenidoRef.current.value
        }

        if (post.titulo === '' || post.contenido === '') {
            
            this.setState({
                mensaje: "Todos los campos son obligarios"
            });

            setTimeout( () => {
                this.setState({
                    mensaje: ""
                });
            },1500);
            return;
            
        }

        this.props.crearPost(post);
        // console.log(this.props.ok);
        
    }



    render() {
        return (

            <form onSubmit={this.crearTarea}>
                <div className="col-11 col-md-8 col-lg-9 form-group">
                    <label className="form-control-label">Titulo de la Tarea</label>
                    <input type="text" ref={this.tituloRef} className="form-control form-control-success" name="Titulo" placeholder="Nombre de la tarea"/>
                </div>
                <div className="col-11 col-md-8 col-lg-9 form-group">
                    <label className="form-control-label">Descripcion de la Tarea</label>
                    <textarea ref={this.contenidoRef} className="form-control form-control-danger" name="nombre" placeholder="Descripcion de la tarea"></textarea>
                </div>
                { this.state.mensaje ? <div className="col-11 form-group alert alert-danger text-center">{this.state.mensaje}</div> : ''}
                <div className="col-12 col-md-8 col-lg-9 form-group justify-content-between d-flex">
                    <Link to={'/'} className="btn btn-info ">&larr; Ver Tareas</Link>
                    <button type="submit" className="btn btn-success">Crear Tarea</button>
                </div>
            </form>
        )
    }
}

export default ContenidoPost;