import React, {Component} from 'react';
import { Link } from 'react-router-dom';

class ContenidoPost extends Component {
    render() {
        let {titulo, contenido} = this.props.verPost;
        return (

            <div className="ver-post col-11 col-md-9 col-lg-7">

                <div className="ver-titulo row mb-2">
                    <h5 className="col-12 my-2">{titulo}</h5>
                </div>
                <div className="ver-contenido row mb-2">
                    <p className="col-10 p-3">{contenido}</p>
                </div>
                <div className="row">
                    <Link to={"/"} className="btn btn-success">&larr; Volver</Link>
                </div>

            </div>
        )
    }
}

export default ContenidoPost;