import React, {Component} from 'react';
import { Link } from 'react-router-dom';

class ContenidoPost extends Component {

    tituloRef = React.createRef();
    contenidoRef = React.createRef();
    state = {
        mensaje: ''
    }
    editarTarea = (e) => {
        e.preventDefault();

        const post = {
            titulo: this.tituloRef.current.value,
            contenido: this.contenidoRef.current.value
        }

        if (post.titulo === '' || post.contenido === '') {
            
            this.setState({
                mensaje: "Todos los campos son obligarios"
            });

            setTimeout( () => {
                this.setState({
                    mensaje: ""
                });
            },1500);
            return;
            
        }
        const { _id } = this.props.verPost;
        this.props.editarPost(_id, post);
    }


    render() {
        const { titulo, contenido } = this.props.verPost;
        return (
            <form onSubmit={this.editarTarea}>
                <div className="col-11 col-md-8 col-lg-9 form-group">
                    <label className="form-control-label">Titulo de la Tarea</label>
                    <input type="text" ref={this.tituloRef} className="form-control form-control-success" defaultValue={titulo} name="Titulo" />
                </div>
                <div className="col-11 col-md-8 col-lg-9 form-group">
                    <label className="form-control-label">Descripcion de la Tarea</label>
                    <textarea ref={this.contenidoRef} className="form-control form-control-danger" defaultValue={contenido} name="nombre" ></textarea>
                </div>
                { this.state.mensaje ? <div className="col-8 form-group alert alert-danger text-center">{this.state.mensaje}</div> : ''}
                <div className="col-12 col-md-8 col-lg-9 form-group justify-content-between d-flex">
                    <Link to={'/'} className=" btn btn-info ">&larr; Ver Tareas</Link>
                    <button type="submit" className="btn btn-success ml-1">Guardar cambios</button>
                </div>
            </form>
        )
    }
}

export default ContenidoPost;