import React, {Component} from 'react';
import Header from './Header';
import ContenidoPost from './ContenidoPost';


class CrearPost extends Component {

    render() {
        return (
            <React.Fragment>

                <Header />
                <ContenidoPost
                    crearPost={this.props.crearPost}
                    
                />
            </React.Fragment>
        )
    }

}

export default CrearPost;