import React, {Component} from 'react';
import Header from './Header';
import ContenidoPost from './ContenidoPost';


class EditarPost extends Component {

    render() {
        return (
            <React.Fragment>

                <Header />
                <ContenidoPost
                    editarPost={this.props.editarPost}
                    verPost={this.props.verPost}
                />
            </React.Fragment>
        )
    }

}

export default EditarPost;