import React, {Component} from 'react';
import Post from './Post';

class ContenedorPosts extends Component {
    render() {
        let posts = this.props.posts;
        return (
            <div className="col-12 p-4">
                {
                    Object.keys(posts).map( (res, index) => (
                        <Post 
                            key={index}
                            post={posts[res]}
                            id={index}
                            mostrarPost={this.props.mostrarPost}
                            borrarPost={this.props.borrarPost}
                        />
                    ) )
                }
            </div>
        )
    }
}

export default ContenedorPosts;